html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  background-color: #efebeb;
  color: #000000;
  font-size: 20px;
  font-family: "PT Serif", serif;
}
.primary-title {
  font-size: 100px;
  font-weight: 700;
  line-height: 90px;
}
.main-text {
  font-family: "Proxima Nova", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
}
.main-button {
  text-decoration: none;
  cursor: pointer;
  background-color: #000000;
  font-size: 18px;
  padding: 13px 21px;
  padding-right: 13px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  min-width: 235px;
  border-radius: 18px;
  color: #fbbf28;
}
.main-button > span {
  display: inline-block;
  margin-left: 42px;
  width: 16px;
  height: 12px;
  background-image: url(../img/arrow-pointing-to-right.png);
  background-size: contain;
  background-repeat: no-repeat;
}
/* pt-serif-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('PT Serif'), local('PTSerif-Regular'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff') format('woff');
}
/* pt-serif-700 - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('PT Serif Bold'), local('PTSerif-Bold'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff') format('woff');
}
@font-face {
  font-family: 'Proxima nova';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Proxima nova'), local('Proxima-nova'), url('../fonts/ProximaNova-Regular.woff2') format('woff2'), url('../fonts/ProximaNova-Regular.woff') format('woff');
}
.start-page {
  background-image: url(../img/text-cloud.png);
  min-height: 100vh;
  background-position: 64px bottom;
  background-repeat: no-repeat;
}
.start-page__header {
  padding-left: 64px;
  padding-top: 42px;
}
.logo {
  text-decoration: none;
  display: inline-block;
  width: 156px;
  height: 48px;
}
.logo__img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.start-page__desc-box {
  display: flex;
}
.start-page__desc-wrap {
  max-width: 488px;
  text-align: center;
  margin-top: 73px;
  margin-right: 108px;
  margin-left: auto;
}
.start-page__slogan {
  font-size: 24px;
  font-weight: 500;
  line-height: 68px;
  margin: 0;
  font-family: "Proxima Nova", sans-serif;
}
.start-page__title {
  margin: 0;
  margin-bottom: 36px;
}
